import Vue from 'vue';
import App from './App.vue';
import VueResources from 'vue-resource';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import VueRouter from 'vue-router';
import global_ from './config/global.js'
import 'xe-utils'
import VXETable from 'vxe-table'
import VXETablePluginElement from 'vxe-table-plugin-element'
import 'vxe-table/lib/index.css'
import 'vxe-table-plugin-element/dist/style.css'
// 可编辑表格 https://gitee.com/xuliangzhan_admin/vxe-table
Vue.use(VXETable)

VXETable.use(VXETablePluginElement)
Vue.prototype.GLOBAL = global_;
// import axios from 'axios'
// import VueAxios from 'vue-axios';


import "bootstrap/dist/css/bootstrap.min.css";
// echarts
import echarts from 'echarts'
Vue.prototype.$echarts = echarts


import SceenStatus from './components/tls/SceenStatus.vue';
import RealTimeRadarData from './components/thick/RealTimeRadarData.vue';
import PreTestSinglePoint from './components/pre/PreTestSinglePoint.vue';
import TaskList from './components/task/TaskList.vue';
import TaskSyn from './components/task/TaskSyn.vue';
import Coalfeeder from './components/tmc/Coalfeeder.vue';
import PlcData from './components/tls/PlcData.vue';
// Vue.use(VueAxios, axios)

Vue.use(VueResources)
Vue.use(ElementUI);
Vue.use(VueRouter)
Vue.use(echarts)

// 配置路由组件
const routes = [
  {path: '/screen/:loadLine', component: SceenStatus },
  {path:'/singlePointWrite/:loadLine', component: PreTestSinglePoint },
  {path:'/realTimeRadarData/:loadLine', component: RealTimeRadarData },
  {path:'/taskNowLoad', component: TaskList},
  {path:'/taskSyn', component: TaskSyn},
  {path:'/coalTool', component: Coalfeeder},
  {path:'/PlcData', component: PlcData}
  
]

// 实例化路由
const router = new VueRouter({
  routes // (缩写) 相当于 routes: routes
})
Vue.config.productionTip = false

new Vue({
  render: h => h(App),
  router,
}).$mount('#app')
