<template>
<div>
  <el-row style="height:100px" ></el-row> 
<el-row type="flex" id="singlePointWrite">
 <el-col :span="8"></el-col>
  
    <el-col :span="8">
    <el-form ref="form" label-width="180px">
       <el-form-item label="测试方式">
         <el-radio v-model="sendMethod" :label="1" border >连续单个发送</el-radio>
         <el-radio v-model="sendMethod" :label="2" border>list集合发送</el-radio>
      </el-form-item>
       
      <el-form-item label="请输入点号">
        <el-input placeholder="请输入点号" v-model="pointId" clearable></el-input>
      </el-form-item>
        <el-form-item label="请输入结束点号">
        <el-input placeholder="请输入结束点号" v-model="pointIdEnd" clearable></el-input>
      </el-form-item>
      <el-form-item label="请输入点值">
        <el-input placeholder="请输入点值" v-model="rawValue" clearable></el-input>
      </el-form-item>
      <el-form-item label="请输入刷新频率">
        <el-input placeholder="请输入刷新频率" v-model="times" clearable></el-input>
      </el-form-item>
      <el-form-item >
        <el-button type="primary" @click="begin()">开始</el-button>
        <el-button type="danger" @click="stop()">停止</el-button>
      </el-form-item>
    </el-form>
    </el-col>
    <el-col :span="8"></el-col>
  </el-row>
</div>
  
</template>

<script>
export default {
  name: "SinglePointWrite",
  data() {
    return {
      pointId: "",
      rawValue: 0,
      pointIdEnd:"",
      times: 500,
      timer: null ,//定时器名称
      sendMethod:1
    };
  },
  methods: {
    begin: function() {
      this.timer = setInterval(this.writePoint, this.times);
    },
    stop() {
      if (this.timer) {
        clearInterval(this.timer); // 清除定时器
        this.timer = null;
      }
    },
    writePoint() {
      let url = "";
      if(this.sendMethod==1){
        url= this.GLOBAL.BASE_URL+"/writePoints";
      }else{
         url= this.GLOBAL.BASE_URL+"/writePointList";
      }
      
      console.log(this.$data)
      this.$http.post(url, this.$data,{emulateJSON: true}).then(
        response => {
            console.log(response)
          this.rawValue = this.rawValue*1 + 1;
          console.log(this.rawValue);
        },
        response => {
          // error callback
          console.log(response);
        }
      );
    }
  }
};
</script>

