<template>
  <div id="tasksyn">{{msg}}</div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      msg: ""
    };
  },
  mounted() {
    this.taskSyn();
  },
  methods: {
    taskSyn() {
         this.msg = "正在同步 请等待。。。。。";
      let url = this.GLOBAL.BASE_URL+"/syncTask";
      this.$http.get(url).then(
        response => {
          var result = response.body;

          this.msg = result.msg;
        },
        response => {
          // error callback
          console.log(response);
        }
      );
    }
  }
};
</script>

