<template>
  <!--  -->

  <div id="Coalfeeder">
    <el-row>
      <el-radio v-model="loadLine" :label="400012" border>1号站</el-radio>
      <el-radio v-model="loadLine" :label="400013" border>2号站</el-radio>
    </el-row>
    <el-row>
      <el-switch
        v-model="allowControl"
        active-text="控制给煤机"
        inactive-text="不控制给煤机"
        active-color="#13ce66"
        inactive-color="#ff4949"
      ></el-switch>
    </el-row>
    <el-divider></el-divider>
    <el-row>
      <v-coalfeeder-single
       v-for="coalfeeder in coalfeeders"
        v-bind:key="coalfeeder.coalFeederCode" 
        :coalfeeder="coalfeeder"
        :loadLine="loadLine"
        :allowControl="allowControl"></v-coalfeeder-single>
    </el-row>
  </div>
</template>

<script>
import CoalfeederSingle from "./CoalfeederSingle.vue";
export default {
  name: "Coalfeeder",
  data() {
    return {
      /* 如果为true,控制就会下发*/
      allowControl: false,
      loadLine: 400012,
      coalfeeders: [],
      timer:null
    };
  },
  methods: {
    getCoalFeeders: function() {
      let url =
        this.GLOBAL.BASE_URL + "/getCoalFeeders?loadLine=" + this.loadLine;
        this.$http.get(url).then(
        response => {
          var result = response.body;
          this.coalfeeders =result;
        },
        response => {
          // error callback
          console.log(response);
        }
      );
    }
  },
  components: {
    "v-coalfeeder-single": CoalfeederSingle
  },
  mounted() {
    this.getCoalFeeders();
    this.timer = setInterval(this.getCoalFeeders, 2000);
  },
  destroyed() {
    if (this.timer) {
      //如果定时器在运行则关闭
      clearInterval(this.timer);
    }
  },
};
</script>

