<template>
  <div id="position">
    <h6>
      定位结果车速:
      <span id="trainV"></span>
    </h6>
    <table class="table table-condensed">
      <thead>
        <tr>
          <td>车厢号</td>
          <td>头坐标</td>
          <td>尾坐标</td>
          <td>头-尾</td>
        </tr>
      </thead>
      <tbody id="trainPosition">
        <tr>
          <td>asdas</td>
          <td>asdas</td>
          <td>asdas</td>
          <td>asdas</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "Position",
  data() {
    return {
      timer: null
    };
  },
  methods: {
    refreshPosition: function() {
      
      let url = this.GLOBAL.BASE_URL+"/getAllPosition?loadLine="+this.loadLine;
      this.$http.get(url).then(
        response => {
          var result = response.body;
          let obj = document.querySelector("#trainPosition");
          if (result && result.length > 0) {
            var html = "";
            for (var i = 0; i < result.length; i++) {
              var carLong = result[i].carHead - result[i].carTail;
              html += "<tr>";
              html += "<td>" + result[i].index + "</td>";
              html += "<td>" + result[i].carHead + "</td>";
              html += "<td>" + result[i].carTail + "</td>";
              html += "<td>" + carLong + "</td>";
              html += "</tr>";
            }
            if (obj) {
              document.querySelector("#trainPosition").innerHTML = html;
            }
          } else {
            if (obj) {
              obj.innerHTML = "";
            }
          }
        },
        response => {
          // error callback
          console.log(response);
          let obj = document.querySelector("#trainPosition");
          if (obj) {
            obj.innerHTML = "";
          }
        }
      );
      let url2 = this.GLOBAL.BASE_URL+"/getTrainV?loadLine="+this.loadLine;
      this.$http.get(url2).then(
        response => {
          var result = response.bodyText;
          var speed = result.substring(0, result.indexOf(".") + 3);
          let obj = document.querySelector("#trainV");
          if (obj) {
            obj.innerHTML = speed;
          }
        },
        response => {
          // error callback
          console.log(response);
        }
      );
    }
  },
  mounted() {
    this.refreshPosition();
    this.timer = setInterval(this.refreshPosition, 200);
  },
  destroyed() {
    if (this.timer) {
      //如果定时器在运行则关闭
      clearInterval(this.timer);
    }
  },props: ['loadLine']
}
</script>

