<template>
  <div id="plcData">
    <div>
      <el-row>
          <el-col :span="2">
          <el-tag >{{loadLine=='400012'?'一号站':'二号站'}}</el-tag>
        </el-col>
        <el-col :span="4">
          <span>刷新频率(ms):&nbsp;&nbsp;&nbsp;</span>
          <el-input v-model="inputTimer" style="width:100px" placeholder="请输入刷新频率"></el-input>
        </el-col>
        <el-col :span="2">
          <el-button @click="beginRefreshPoint()">启动检测</el-button>
        </el-col>
        <el-col :span="2">
          <el-button @click="stopRefreshPoint()" type="danger">停止观察</el-button>
        </el-col>
        <el-col :span="2">
          <el-tag id="runStatus" :type="runStatus=='stop'?'danger':'success'" >{{runStatus}}</el-tag>
        </el-col>
         
      </el-row>
      <el-divider />
      <el-checkbox-group v-model="checkDevices">
        <el-checkbox
          v-for="device in devices"
          v-bind:key="device.deviceId"
          v-model="device.deviceId"
          :label="device.deviceId"
          border
        >{{device.deviceName}}</el-checkbox>
        <el-button
          type="danger"
          style="float:right"
          icon="el-icon-refresh-right"
          circle
          @click="clearCheck()"
        >清空</el-button>
      </el-checkbox-group>
      <el-divider />
      <div>
        <vxe-table :data="editTableData" @cell-dblclick="cellDBLClickEvent">
          <vxe-table-column type="seq" title="序号" width="80"></vxe-table-column>
         
          <vxe-table-column field="pointId" title="点id"></vxe-table-column>
         
          <vxe-table-column field="pointName" title="点描述"></vxe-table-column>
          <vxe-table-column field="pointValue" title="点值"></vxe-table-column>
          <vxe-table-column title="操作" width="100" show-overflow>
            <template v-slot="{ row }">
              <el-button type="primary" icon="el-icon-edit"  circle @click="editEvent(row)"></el-button>
           </template>
          </vxe-table-column>
        </vxe-table>
        <vxe-modal v-model="showEdit" title="修改点值" width="800" resize destroy-on-close>
          <vxe-form
            :data="formData"
            :items="formItems"
            title-align="right"
            title-width="100"
            :loading="submitLoading"
            @submit="submitEvent"
          ></vxe-form>
        </vxe-modal>
      </div>
      <el-divider />
      <div>
        <vxe-table :data="onlyReadTableData">
          <vxe-table-column type="seq" title="序号" width="80"></vxe-table-column>
          <vxe-table-column field="pointId" title="点id"></vxe-table-column>
          <vxe-table-column field="pointName" title="点描述"></vxe-table-column>
          <vxe-table-column field="pointValue" title="点值"></vxe-table-column>
        </vxe-table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      timer: null,
      name: "PlcData",
      loadLine: null,
      devices: [],
      inputTimer: 500,
      checkDevices: [],
      editTableData: [],
      onlyReadTableData: [],
      runStatus: "stop",
      selectRow: null,
      showEdit: false,
      submitLoading:false,
      formData: {
        loadLine:null,
        pointId: null,
        pointName: null,
        pointValue: null
      },
      formItems: [
        {
          title: "输入点值提交发送",
          span: 24,
          titleAlign: "left",
          titleWidth: 200,
          titlePrefix: { icon: "fa fa-address-card-o" }
        },
        {
          field: "loadLine",
          title: "装车线",
          span: 24,
          itemRender: {
            name: "$input",
            props: { placeholder: "装车线", disabled: true}
          }
        },
        {
          field: "pointId",
          title: "点号",
          span: 24,
          itemRender: {
            name: "$input",
            props: { placeholder: "请输入点Id", disabled: true}
          }
        },
        {
          field: "pointName",
          title: "点描述",
          span: 24,
          itemRender: {
            name: "$input",
            props: { placeholder: "请输入点描述", disabled: true }
          }
        },
        {
          field: "pointValue",
          title: "值",
          span: 24,
          itemRender: { name: "$input", props: { placeholder: "请输入新值" } }
        },
        {
          align: "center",
          span: 24,
          titleAlign: "left",
          itemRender: {
            name: "$buttons",
            children: [
              { props: { type: "submit", content: "提交", status: "primary" } },
              ]
          }
        }
      ]
    };
  },
  methods: {
    submitEvent() {
      // 有了这个属性会显示一个提交动画
      this.submitLoading = true;
      let url =  url= this.GLOBAL.BASE_URL+"/writeSinglePoint";
      this.$http.post(url, this.selectRow).then(
        response => {
           
           this.showEdit = false;
           this.submitLoading=false;
           this.$XModal.message({ message: response.body.msg, status: "success" });
        },
        response => {
          // error callback
          console.log(response);
          this.$XModal.message({ error: "fail" });
        }
      );

    },
    cellDBLClickEvent({ row }) {
      this.editEvent(row);
    },
    editEvent(row) {
      this.formData = {
        loadLine: row.loadLine,
        pointId: row.pointId,
        pointName: row.pointName,
        pointValue: row.pointValue
      };
      this.selectRow = row;
      this.showEdit = true;
    },
    refreshTab: function() {
      let url = this.GLOBAL.BASE_URL + "/devices";
      this.$http.post(url).then(
        response => {
          var result = response.body;
          this.devices = result;
        },
        response => {
          // error callback
          console.log(response);
        }
      );
    },
    checkAllowRefresh: function() {
      if (this.checkDevices.length == 0) {
        this.stopRefreshPoint();
        this.$message({
          message: '没有选择监测设备',
          type: 'warning'
        });
        return false;
      } else {
        return true;
      }
    },
    refreshPoints: function() {
      // console.log(this.checkDevices);
      if (!this.checkAllowRefresh()) return;

      let url = this.GLOBAL.BASE_URL + "/points?loadLine=" + this.loadLine;
      this.$http.post(url, JSON.stringify(this.checkDevices)).then(
        response => {
          var result = response.body;
          this.onlyReadTableData = result.or;
          this.editTableData = result.wr;
        },
        response => {
          // error callback
          console.log(response);
        }
      );
    },
    beginRefreshPoint: function() {
      if (this.timer) return;
      if (!this.checkAllowRefresh()) return;
      this.refreshPoints();
      this.timer = setInterval(this.refreshPoints, this.inputTimer);
      this.runStatus = "running";
    },
    stopRefreshPoint: function() {
      if (this.timer) {
        //如果定时器在运行则关闭
        clearInterval(this.timer);
        this.timer = null;
        this.runStatus = "stop";
        // 清空的功能可以保留
        //  this.onlyReadTableData=[];
        //  this.editTableData=[];
      }
    },
    clearCheck: function() {
      this.checkDevices = [];
    }
  },
  mounted() {
    this.loadLine = this.$route.query.loadLine;
    // console.log(this.loadLine);
    this.refreshTab();
  },
  destroyed() {
    this.stopRefreshPoint();
  },
  watch: {
    // 方法1
    $route() {
      //监听路由是否变化
      if (this.$route.query.loadLine) {
        // 判断条件1  判断传递值的变化
        this.loadLine = this.$route.params.loadLine;

        // console.log(this.loadLine);
        // this.refreshTab();
      }
    },

    checkDevices(val) {
      //普通的watch监听
      if (val.length == 0) {
        if (this.timer != null) {
          this.$message("勾选设备被清空，下次需要重新点击启动");
        }
        this.stopRefreshPoint();
      }
    }
  }
};
</script>

