<template>
  <div id="radarGraph">
    <span id="chartTip"></span>
    <div id="myChart"></div>
  </div>
</template>

<script>
export default {
  name: "RadarGraph",
  data() {
    return {
      myChart:null
    };
  },
  methods: {
    getRadarGraph() {
      let url =
        this.GLOBAL.BASE_URL+"/getRadarDataGraph?loadLine=" +
        this.loadLine +
        "&taskNum=" +
        this.taskNum +
        "&trainNum=" +
        this.trainNum;
      this.$http.post(url).then(
        response => {
          let result = response.body;
          console.log(result)
          if(!result||result.length==0){
            document.getElementById("chartTip").innerHTML="暂无数据";
            if(this.myChart){
              this.myChart.clear();
              this.myChart.dispose();
              this.myChart = null;
            }
          
            return;
          }
          console.log(result);
          let keys = new Array();
          let values = new Array();
          for (let i = 0; i < result.length; i++) {
            keys.push(result[i].position);
            values.push(result[i].unbalanceValue);
          }
           document.getElementById("chartTip").innerHTML="";
          this.myChart = this.$echarts.init(document.getElementById("myChart"));
          let option = {
            title: {
              text: "车厢二维雷达图"
            },
            tooltip: {},
            xAxis: {
              data: keys,
              boundaryGap: false,
            },
            yAxis: {
               type: 'value'
            },
            series: [
              {
                name: "点号",
                type: "line",
                data: values,
                areaStyle: {}
               }
            ]
          };

          // 使用刚指定的配置项和数据显示图表。
          this.myChart.setOption(option);
          console.log("图形初始化成功");
        },
        response => {
          // error callback
          console.log(response);
        }
      );
    }
  },
  props: ["loadLine", "taskNum", "trainNum"],
  mounted() {
    this.getRadarGraph();
  },watch: {
    // 方法1
    trainNum() {
      //监听路由是否变化
      if (this.trainNum) {
        this.getRadarGraph();
      }
    }
  }
};
</script>

