<template>
  <div id="tasks">
    <h6>查看正在装车的列表</h6>
            <el-dialog
  title="车厢列表"
  :visible.sync="dialogVisible"
  width="40%"
 >
  <v-carrige-list  ref="carriageListPage"  :loadLine="queryLoadLine" :taskNum="queryTaskNum" > </v-carrige-list>

           </el-dialog>
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column fixed prop="trainNum" label="车次号" width="150"></el-table-column>
        <el-table-column prop="loadLineName" label="装车线" width="120"></el-table-column>

        <el-table-column prop="trainNumTotal" label="总节数" width="120"></el-table-column>

        <el-table-column label="操作" width="100">
         
          <template slot-scope="scope">    
           
          
        <el-button    slot="reference"      type="text"  size="small"  @click="queryCarriageList(scope.row)"  >查看车厢列表</el-button>
       
        
       
        </template> 
        </el-table-column>
      </el-table>
   
   
  </div>
 
</template>

<script>
import CarriageList from "./CarriageList.vue";
export default {
  methods: {

    queryCarriageList(task){
       this.dialogVisible = true;
       this.queryLoadLine = task.loadLine;
       this.queryTaskNum = task.taskNum;
     
      // this.$refs.carriageListPage.getCarriageList();
      
    },
     
    // 查询正在装车的数据
    showTaskLoadNow() {
      let url = this.GLOBAL.BASE_URL+"/taskList";
      this.$http.get(url).then(
        response => {
          var result = response.body;

          this.tableData = result;
        },
        response => {
          // error callback
          console.log(response);
        }
      );
    }
  },

  data() {
    return {
      tableData: [],
      dialogVisible:false,
      queryLoadLine:"",
      queryTaskNum:""
    };
  },
  components: {
    "v-carrige-list": CarriageList
  },mounted(){

    this.showTaskLoadNow();
  }
};
</script>

