<template>
  <div id="screen">
    <el-container>
      <el-header>
        <div>
          <el-radio v-model="activeScreen" :label="1" border >主光幕</el-radio>
         <el-radio v-model="activeScreen" :label="2" border>备用光幕</el-radio>
        </div>
        
      </el-header>
      <el-main>
        <v-position :loadLine="loadLine"></v-position>
        <table class="table-bordered table-hover table-condensed">
          <thead>
            <th v-for="index in 40 " :key="index">
              <span v-if="index <10 ">{{'0'+index}}</span>
              <span v-else>{{index}}</span>
            </th>
          </thead>
          <tbody id="screenTable">
            <tr>
              <td></td>
            </tr>
          </tbody>
        </table>
        <v-important-data :loadLine="loadLine"></v-important-data>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import Position from "./Position.vue";
import ImportantData from "./ImportantData.vue";
export default {
  name: "Screen",
  data() {
    return {
      timer: null,
      loadLine: "",
      activeScreen: 1
    };
  },
  methods: {
    refreshScreen: function() {
      // 请求光幕数据
      let url =
        this.GLOBAL.BASE_URL+"/screenData?loadLine=" +
        this.loadLine +
        "&activeScreen=" +
        this.activeScreen;
      this.$http.post(url).then(
        response => {
          var result = response.body;

          var html = "";
          for (var i = 0; i < result.length; i += 40) {
            html += "<tr>";
            for (var j = 0; j < 40; j++) {
              if (result[i + j] == 1) {
                html +=
                  '<td style="background:#000; color:#FFF">' +
                  result[i + j] +
                  "</td>";
              } else {
                html += "<td>" + result[i + j] + "</td>";
              }
            }
            html += "</tr>";
          }
          document.querySelector("#screenTable").innerHTML = html;
        },
        response => {
          // error callback
          console.log(response);
        }
      );
    }
  },
  mounted() {
    this.loadLine = this.$route.params.loadLine;
    this.refreshScreen();
    this.timer = setInterval(this.refreshScreen, 200);
  },
  components: {
    "v-position": Position,
    "v-important-data":ImportantData
  },
  destroyed() {
    if (this.timer) {
      //如果定时器在运行则关闭
      clearInterval(this.timer);
    }
  },
  watch: {
    // 方法1
    $route() {
      //监听路由是否变化
      if (this.$route.params.loadLine) {
        // 判断条件1  判断传递值的变化
        this.loadLine = this.$route.params.loadLine;
      }
    }
  }
};
</script>

