<template>
 <div id="ImporaImportantData">
   <div  class="tag-group"> <span class="tag-group-title">定量仓重量</span><el-tag size="small"  type="warning">{{weight}}</el-tag></div>
  <div  class="tag-group"> <span class="tag-group-title">激光测距仪</span> <el-tag size="small"  type="danger">{{length1}} -- {{length2}}</el-tag></div>

  <div  class="tag-group"> <span class="tag-group-title">手自动状态</span> <el-tag size="small"  type="danger">{{softAuto}}</el-tag></div>
 
  <el-row>
  
  <el-button type="primary">切换自动</el-button>
 
  <el-button type="danger">切换手动</el-button>
</el-row>
 </div>
</template>

<script>
export default {
  name: "ImportantData",
  data() {
    return {
      timer: null,
      weight:null,
      softAuto:null,
      length1:null,
      length2:null
    };
  },
  methods: {
    refreshImporaImportantData: function() {
      
      let url = this.GLOBAL.BASE_URL+"/refreshImportantData?loadLine="+this.loadLine;
      this.$http.get(url).then(
        response => {
          console.log(response)
          var result = response.body;
          this.weight=result.weight;
          this.softAuto=result.softAuto;
          this.length1= result.length1;
      this.length2= result.length2;
        },
        response => {
          // error callback
          console.log(response);
          
        }
      );
     
    }
  },
  mounted() {
    this.refreshImporaImportantData();
    this.timer = setInterval(this.refreshImporaImportantData, 200);
  },
  destroyed() {
    if (this.timer) {
      //如果定时器在运行则关闭
      clearInterval(this.timer);
    }
  },props: ['loadLine']
}
</script>

