<template>
  <el-col :span="3">
    <el-card :body-style="{ padding: '0px' }">
      <div style="padding: 14px;">
        <div>
          <div>
            <span>所属煤仓:</span>
            {{coalfeeder.coalBunker}}
          </div>
          <div>
            <span>煤机编号:</span>
            {{coalfeeder.coalFeederCode}}
          </div>
          <div>
            <span>煤机状态:</span>
            <span v-if="coalfeeder.runStatus" style="color:#67C23A">运行</span>
            <span v-else style="color:#909399">停止</span>
          </div>
          <div>
            <span>反馈频率:</span>
            {{coalfeeder.outputRunHz}}
          </div>

          <div title="滑动输入频率">
            <span>频率控制:</span>
            <i class="el-icon-bottom"></i>
          </div>
          <div class="block">
            <el-slider
              title="滑动输入频率"
              v-model="coalfeeder.inputRunHz"
              :step="1"
              :max="50"
              size="mini"
              show-stops
              @change="updateFeederRunHz()"
            ></el-slider>
          </div>
        </div>
        <div class="bottom clearfix">
          <el-button type="success" @click="start()" size="mini" class="button">启动</el-button>
          <el-button type="warning" @click="stop()" size="mini" class="button">停止</el-button>
          <el-button type="primary" @click="reset()" size="mini" class="button">复位</el-button>
        </div>
      </div>
    </el-card>
  </el-col>
</template>

<script>
export default {
  data() {
    return {
      value: 0
    };
  },
  methods: {
    start: function() {
      if (this.checkAllowControl()) {
        console.log("this.allowControl"+this.allowControl)
        let url = this.GLOBAL.BASE_URL + "/startCoalFeeder";
        console.log(this.$data);
        this.$http.post(url, this.coalfeeder, { emulateJSON: true }).then(
          response => {
            console.log(response);
         
          },
          response => {
            // error callback
            console.log(response);
          }
        );
      } 
    },
    stop: function() {
      if (this.checkAllowControl()) {
        let url = this.GLOBAL.BASE_URL + "/stopCoalFeeder";
        console.log(this.$data);
        this.$http.post(url, this.coalfeeder, { emulateJSON: true }).then(
          response => {
            console.log(response);
          },
          response => {
            // error callback
            console.log(response);
          }
        );
      } 
    },
    reset: function() {
      if (this.checkAllowControl()) {
        let url = this.GLOBAL.BASE_URL + "/resetCoalFeeder";
        console.log(this.$data);
        this.$http.post(url, this.coalfeeder, { emulateJSON: true }).then(
          response => {
            console.log(response);
          },
          response => {
            // error callback
            console.log(response);
          }
        );
      } 
    },
    updateFeederRunHz: function() {
      if (this.checkAllowControl()) {
        let url = this.GLOBAL.BASE_URL + "/updateFeederRunHz";
        console.log(this.$data);
        this.$http.post(url, this.coalfeeder, { emulateJSON: true }).then(
          response => {
            console.log(response);
            this.rawValue = this.rawValue * 1 + 1;
            console.log(this.rawValue);
          },
          response => {
            // error callback
            console.log(response);
          }
        );
      } 
    },checkAllowControl: function(){
      if (this.allowControl) {return true}else{
this.$message({
          message: '不允许控制给煤机',
          type: 'warning'
        });
        return false;
      }
    }
  },
  props: {
    coalfeeder: {
      type: Object
    },
    loadLine: {
      type: Number
    },
    allowControl: {
      type: Boolean
    }
  },

  mounted() {}
};
</script>

