<template>
  <div id="carriages">
    

 <el-table :data="carriageData" border height="400px" width = "450px">
      <el-table-column fixed prop="orderNum" label="序号" width="150"></el-table-column>
      <el-table-column fixed prop="trainNum" label="车厢号码" width="250"></el-table-column>

      <el-table-column label="操作" width="200">
        <template slot-scope="scope">
          <el-button @click="getRadarData(scope.row)" type="text" size="small">查看偏载图形</el-button>
        </template>
      </el-table-column>
    </el-table>
<el-dialog
      width="60%"
      title="雷达图"
      :visible.sync="innerVisible"
      append-to-body>
      <v-radar ref="radarGraph" :loadLine="loadLine" :taskNum="taskNum"  :trainNum="queryTrainNum"  ></v-radar>
    </el-dialog>

  </div>
</template>  

<script>
import RadarGraph from "../thick/Radar3d.vue"
export default {
  name: "CarriageList",
  data() {
    return {
      carriageData: [],
      innerVisible:false,
      queryTrainNum:""
     
    };
  },
  methods: {
    getCarriageList() {
      let url =
        this.GLOBAL.BASE_URL+"/queryCarriage?loadLine=" +
        this.loadLine +
        "&taskNum=" +
        this.taskNum;
      this.$http.post(url).then(
        response => {
          var result = response.body;

          console.log(result);
          this.carriageData = result;
        },
        response => {
          // error callback
          console.log(response);
        }
      );
    },

    getRadarData(carriage) {
      
      this.loadLine =carriage.loadLine;
      this.taskNum = carriage.taskNum;
      let trainNum = carriage.trainNum;
      this.innerVisible = true;
      this.queryTrainNum = trainNum;  
     
      
    }
  },
  mounted() {
    this.getCarriageList();
  },
  destroyed() {},
  props: ["loadLine", "taskNum"],
  components:{
    "v-radar":RadarGraph
  },watch: {
    // 方法1
    'taskNum'() {
      //监听路由是否变化
      if (this.taskNum) {
        this.getCarriageList();
      }
    }
  }
};
</script>

