<template>
  <div id="myChart"></div>
</template>

<script>
export default {
  name: "RealTimeRadarData",
  data() {
    return {
      times: 2000,
      timer:null,
      loadLine:"400012"
    };
  },
  methods: {
    refreshRealTimeRadarData: function() {
      let url = this.GLOBAL.BASE_URL+"/realTimeRadarData?loadLine="+this.loadLine;
      this.$http.get(url).then(
        response => {
          var result = response.body;
          console.log(result);
          let myChart = this.$echarts.init(document.getElementById("myChart"));
          var option = {
            title: {
              text: "西克测厚雷达实时数据"
            },
            tooltip: {},
            legend: {
              data: ["值"]
            },
            xAxis: {
              data: result.keys,
              splitLine: {
                show: false
              },
              splitArea: {
                show: false
              }
            },
            yAxis: {
              splitArea: {
                show: false
              }
            },
            series: [
              {
                name: "点号",
                type: "bar",
                data: result.value,
                large: true
              }
            ]
          };

          // 使用刚指定的配置项和数据显示图表。
          myChart.setOption(option);
          console.log("图形初始化成功");
        },
        response => {
          // error callback
          console.log(response);
        }
      );
    }
  },
  mounted() {
    this.refreshRealTimeRadarData();
    setInterval(this.refreshRealTimeRadarData, 200);
  },
   destroyed(){
            if(this.timer) { //如果定时器在运行则关闭
                clearInterval(this.timer); 
            }
        },
  watch: {
    // 方法1
    '$route'() {
      //监听路由是否变化
      if (this.$route.params.loadLine) {
        // 判断条件1  判断传递值的变化
        this.loadLine = this.$route.params.loadLine;
      }
    }
  }

};
</script>

<style>
#myChart {
  width: 800px;
  height: 500px;
}
</style>
